:root {
  --grid-rows: 1;
  --grid-cols: 1;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body, div {
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  height: 100vh;
  width: 100vw;
  flex-direction: row-reverse;
  gap: 60px;
  position: fixed;
}

sup {
  vertical-align: top;
  font-size: .7rem;
  position: relative;
  top: -.3rem;
}

#rules {
  line-height: 1.3em;
}

#rules #rules-header {
  font-size: 1.2rem;
}

#calc-input {
  font-size: 1rem;
}

#calc-output, .grid-buttons {
  font-size: 2rem;
}

button {
  font-size: 1.5rem;
}

#calculator-grp, #bottom-grp, #rules {
  flex-direction: column;
}

#calculator-grp {
  gap: 30px;
}

#calculator-grp #app-header {
  text-align: center;
  text-decoration: underline;
}

#bottom-grp {
  gap: 20px;
  margin-top: 20px;
}

#bottom-grp #rules {
  width: 600px;
  white-space: normal;
  border: 1px solid #000;
  gap: 10px;
  padding: 10px;
}

#bottom-grp #rules ul {
  margin-left: 20px;
}

#move-calc-btn {
  cursor: pointer;
  background-color: #b4b4b4;
  border: 1px solid #000;
  padding: 1rem;
}

#calculator {
  min-height: 450px;
  min-width: 325px;
  flex-direction: column;
  position: relative;
}

#calculator #calc-display, #calculator #calc-buttons {
  width: 100%;
  position: absolute;
}

#calculator #calc-display {
  height: 20%;
  background-color: #5ec496;
  border: 1px solid #000;
  flex-direction: column;
  gap: 5px;
  padding: 15px;
  top: 0;
}

#calculator #calc-display #calc-input, #calculator #calc-display #calc-output {
  width: 100%;
  text-overflow: ellipsis;
  display: block;
  overflow: hidden;
}

#calculator #calc-display #calc-input {
  height: 30%;
}

#calculator #calc-display #calc-output {
  text-align: right;
  height: 50%;
}

#calculator #calc-buttons {
  height: 80%;
  justify-content: initial;
  align-items: initial;
  grid-template-rows: repeat(var(--grid-rows), 1fr);
  grid-template-columns: repeat(var(--grid-cols), 1fr);
  display: grid;
  bottom: 0;
}

#calculator #calc-buttons .grid-buttons {
  border: 1px solid #000;
}

#calculator #calc-buttons .grid-buttons:hover {
  background-color: #007fff66;
}

#calculator #calc-buttons .grid-buttons:active {
  background-color: #007fff33;
}

@media (max-width: 600px) {
  body {
    background-color: #282828;
    gap: 0;
  }

  body #calculator-grp {
    height: 100%;
    width: 100%;
  }

  body #calculator-grp #app-header {
    display: none;
  }

  body #calculator-grp #calculator {
    width: 100%;
    height: 80%;
    min-width: 0;
    min-height: 0;
    background-color: #fff;
  }

  body #calculator-grp #calculator #calc-buttons .grid-buttons {
    font-size: 1.5em;
  }

  body #bottom-grp {
    display: none;
  }
}

@media (max-width: 1000px) {
  body {
    flex-direction: column;
  }

  body #bottom-grp #move-calc-btn {
    display: none;
  }
}

